const jsSliderOffers = new Swiper('.jsSliderOffers .slider__inner', {
	modules: [Navigation, Pagination, Autoplay],
	pagination: {
		el: ".jsSliderOffers .swiper-pagination",
        bulletClass: "slider__pagination-item",
		clickable: true,

	},

    navigation: {
        nextEl: '.jsSliderOffers .swiper-button-next',
        prevEl: '.jsSliderOffers .swiper-button-prev',
    },

	loop: true,
	slidesPerView: 1,
	speed: 600,
	centeredSlides: true,
    spaceBetween: 25,   

	breakpoints: {
        0:{
            pagination: {
                el: ".jsSliderOffers .swiper-pagination",
                bulletClass: "slider__pagination-item",
                clickable: true,
        
            },
        },
		992: {
            pagination: {
                bulletClass: "slider__fraction-item",
                renderBullet: function(index, test){
                    let thisIndex = index + 1;
                    return '<span class="slider__fraction-item"> 0' + thisIndex + '</span>'
                },
                bulletActiveClass: "slider__fraction-item--active",
            },
		},
	}
});

const jsSliderIntro = new Swiper('.jsSliderIntro .slider__inner', {
	modules: [Navigation, Pagination, Autoplay],
	pagination: {
		el: ".jsSliderIntro .swiper-pagination",
        bulletClass: "slider__pagination-item",
		clickable: true,
	},

    navigation: {
        nextEl: '.jsSliderIntro .swiper-button-next',
        prevEl: '.jsSliderIntro .swiper-button-prev',
    },

	loop: true,
	slidesPerView: 1,
	speed: 600,
    spaceBetween: 45,   
});
